<template>
	<v-dialog
		fullscreen
		persistent
		transition="dialog-bottom-transition"
		:value="value"
		@keydown.esc="close">

		<v-card>
			<v-toolbar>
				<v-icon>account_circle</v-icon>
				<v-toolbar-title>Add User</v-toolbar-title>
				<v-spacer />
				<v-btn icon @click="close">
					<v-icon>close</v-icon>
				</v-btn>
			</v-toolbar>
			<v-card-text>
				<v-alert outline :color="messageColor" :value="Boolean(message)">{{ message }}</v-alert>
				<v-form
					v-model="valid"
					ref="form"
					lazy-validation
					@submit="submit"
					>
					<v-text-field
						validate-on-blur
						:color="color"
						label="Username"
						hint="4 to 20 characters of a-zA-Z0-9_"
						ref="username"
						v-model="username"
						:rules="usernameRules"
						prepend-icon="person"
						autofocus
						clearable
						required />
					<v-text-field
						validate-on-blur
						:color="color"
						type="password"
						label="Password"
						hint="4 to 20 characters of 0x21-0x7E in ASCII"
						v-model="password"
						:rules="passwordRules"
						prepend-icon="vpn_key"
						clearable
						required />
					<v-text-field
						validate-on-blur
						:color="color"
						type="password"
						label="Repeat Password"
						v-model="repassword"
						:rules="repasswordRules"
						prepend-icon="vpn_key"
						clearable
						required
						/>
					<v-btn dark :color="color" type="submit">
						Add
					</v-btn>
				</v-form>
			</v-card-text>
		</v-card>
	</v-dialog>
</template>

<script>
import axios from 'axios';
import * as validate from '../util/validate.js';

export default {
	props: {
		value: {
			type: Boolean,
			default: false
		},
		color: {
			type: String,
			default: 'deep-purple lighten-1'
		}
	},
	data()
	{
		return {
			valid: true,
			message: '',
			messageColor: 'error',
			username: '',
			password: '',
			repassword: '',
			usernameRules: [
				validate.usernameValidate
			],
			passwordRules: [
				validate.passwordValidate
			],
			repasswordRules: [
				val => this.repassword === this.password ? true : 'Passwords are not the same'
			],
		};
	},
	watch: {
		/* Watch prop value
		 * If value is true, focus on username
		 */
		value(val)
		{
			if(val)
			{
				requestAnimationFrame(() => {
					this.$refs.username.focus();
				});
			}
		}
	},
	methods: {
		close()
		{
			// In case it is validated on next open
			this.$emit('input', false);
			this.$refs.form.reset();
		},
		async submit()
		{
			try
			{
				if(this.$refs.form.validate())
				{
					const res = (await axios.post('/api/register', {
						username: this.username,
						password: this.password
					})).data;
					if(res.success)
					{
						this.$emit('success', true);
						this.message = '';
						this.close();
					}
					else
					{
						this.message = res.data;
						this.messageColor = 'error';
					}
				}
			}
			catch(err)
			{
				this.message = err.response.data;
				this.messageColor = 'error';
			}
		}
	}
};
</script>
