<template>
	<v-dialog
		persistent
		max-width="500px"
		:value="value"
		@keydown.esc="close">

		<v-card>
			<v-toolbar>
				<v-icon>account_circle</v-icon>
				<v-toolbar-title>Log in</v-toolbar-title>
				<v-spacer />
				<v-btn icon @click="close">
					<v-icon>close</v-icon>
				</v-btn>
			</v-toolbar>
			<v-card-text>
				<v-alert outline :color="messageColor" :value="Boolean(message)">{{ message }}</v-alert>
				<v-form
					v-model="valid"
					ref="form"
					lazy-validation
					@submit="submit">
					<v-text-field
						validate-on-blur
						:color="color"
						type="text"
						label="Username or Email"
						v-model="username"
						ref="username"
						:rules="usernameRules"
						prepend-icon="person"
						clearable
						required
						/>
					<v-text-field
						validate-on-blur
						:color="color"
						type="password"
						label="Password"
						v-model="password"
						:rules="passwordRules"
						prepend-icon="vpn_key"
						clearable
						required
						/>
					<v-btn dark :color="color" type="submit">
						Log in
					</v-btn>
				</v-form>
			</v-card-text>
		</v-card>
	</v-dialog>
</template>

<script>
import axios from 'axios';
import * as validate from '../util/validate.js';

export default {
	props: {
		value: {
			type: Boolean,
			default: false
		},
		color: {
			type: String,
			default: 'primary'
		}
	},
	data()
	{
		return {
			valid: true,
			username: '',
			password: '',
			visible: false,
			message: '',
			messageColor: '',
			usernameRules: [
				val => validate.usernameValidate(val) || validate.emailValidate(val)
			],
			passwordRules: [
				validate.passwordValidate
			]
		};
	},
	watch: {
		/* Watch prop value
		 * If value is true, focus on username
		 */
		value(val)
		{
			if(val)
			{
				requestAnimationFrame(() => {
					this.$refs.username.focus();
				});
			}
		}
	},
	methods: {
		close()
		{
			// In case it is validated on next open
			this.$refs.username.blur();
			this.$emit('input', false);
			this.$refs.form.reset();
		},
		async submit()
		{
			try
			{
				if(this.$refs.form.validate())
				{
					// Don't use await here since it will block
					const res = (await axios.post('/api/login', {
						username: this.username,
						password: this.password
					})).data;
					if(res.success)
					{
						this.$emit('success', true);
						this.message = '';
						this.close();
					}
					else
					{
						this.messageColor = 'error'
						this.message = 'Wrong username or password';
					}
				}
			}
			catch(err)
			{
				this.messageColor = 'error';
				this.message = err.response.data;
			}
		},
		register()
		{
			this.$emit('register', true);
			this.close();
		}
	}
};
</script>

