<template>
	<v-app class="fill-height">
		<login-dialog
			:value="loginDialog"
			@input="val => { setLoginDialog(val); }"
			@success="updateUserInfo"
			/>
		<register-dialog
			:value="registerDialog"
			@input="val => { setRegisterDialog(val); }"
			@success="messageDialog = true" />
		<message-dialog
			persistent
			title="Message"
			v-model="messageDialog"
			message="Add user successfully" />

		<v-toolbar
			dark
			:color="color">
			<v-btn icon title="Home" to="/">
				<v-icon>home</v-icon>
			</v-btn>
			<v-toolbar-title>
				{{ pageTitle }}
			</v-toolbar-title>
			<v-spacer />

			<v-menu v-if="userInfo" offset-y>
				<v-btn flat style="text-transform: none !important;" slot="activator">
					<v-icon medium left>account_circle</v-icon>
					<div style="font-weight: 400" class="title">{{ userInfo }}</div>
				</v-btn>
				<v-list>
					<v-list-tile
						v-for="item in userMenu"
						v-show="item.user ? userInfo === 'admin' : true"
						:key="item.title"
						@click="item.action">
						<v-list-tile-action>
							<v-icon>{{ item.icon }}</v-icon>
						</v-list-tile-action>
						<v-list-tile-content>
							<v-list-tile-title>
								{{ item.title }}
							</v-list-tile-title>
						</v-list-tile-content>
					</v-list-tile>
				</v-list>
			</v-menu>
			<v-btn
				v-else
				flat
				style="text-transform: none !important;"
				@click.stop="setLoginDialog(!loginDialog)">
				<v-icon medium left>account_circle</v-icon>
				<div style="font-weight: 400" class="title">Log in</div>
			</v-btn>
		</v-toolbar>

		<v-content>
			<router-view />
		</v-content>

		<!-- v-footer class="justify-center" dark>
			<span>&copy;2018 DCsunset</span>
		</v-footer -->
	</v-app>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import axios from 'axios';
import LoginDialog from '@/components/LoginDialog.vue';
import RegisterDialog from '@/components/RegisterDialog.vue';
import MessageDialog from '@/components/MessageDialog.vue';

export default {
	components: {
		'login-dialog': LoginDialog,
		'register-dialog': RegisterDialog,
		'message-dialog': MessageDialog,
	},
	data()
	{
		return {
			color: 'grey darken-4',
			messageDialog: false,
			userMenu: [
				{
					user: 'admin',
					icon: 'people',
					title: 'Add User',
					action: () => this.setRegisterDialog(true)
				},
				{
					icon: 'exit_to_app',
					title: 'Logout',
					action: this.logout
				}
			]
		};
	},
	computed: {
		...mapState(['pageTitle', 'userInfo', 'loginDialog', 'registerDialog'])
	},
	methods: {
		...mapMutations(['setUserInfo', 'setLoginDialog', 'setRegisterDialog']),
		errorHandle(err)
		{
			this.$router.push({
				path: '/error',
				query: {
					status: err.status,
					statusText: err.statusText,
					data: err.data
				}
			});
		},
		logout()
		{
			axios.get('/api/logout')
				.then(res => {
					this.setUserInfo(null);
					this.$router.push('/');
				})
				.catch(err => {
					this.errorHandle(err.response);
				});
		},
		updateUserInfo()
		{
			axios.get('/api/user')
				.then(res => {
					if(res.data.success)
					{
						this.setUserInfo(res.data.data);
					}
					else
						this.setUserInfo(null);
				})
				.catch(err => {
					this.errorHandle(err.response);
				});
		}
	},
	mounted()
	{
		this.updateUserInfo();
	}
};
</script>
