<template>
	<v-dialog
		:persistent="persistent"
		max-width="300px"
		:value="value"
		@keydown.esc="close">

		<v-card>
			<v-card-title class="headline">{{ title }}</v-card-title>
			<v-card-text>{{ message }}</v-card-text>
			<v-card-actions>
				<v-spacer />
				<v-btn
					v-for="btn in buttons"
					:key="btn"
					flat
					color="green darken-1"
					@click="$emit(btn.toLowerCase(), true); close();">
					{{ btn }}
				</v-btn>
			</v-card-actions>
		</v-card>
	</v-dialog>
</template>

<script>
export default {
	props: {
		value: {
			type: Boolean,
			default: false
		},
		persistent: {
			type: Boolean,
			default: false
		},
		buttons: {
			type: Array,
			default: () => ['ok']
		},
		title: {
			type: String,
			default: ''
		},
		message: {
			type: String,
			default: ''
		},
	},
	methods: {
		close()
		{
			this.$emit('input', false);
		}
	}
};
</script>
